/*BODY*/
@import url("https://fonts.googleapis.com/css?family=Didact+Gothic");

body {
  font-family: "Didact Gothic", sans-serif;
  background-color: #eeeeee;
}

/*LAYOUT*/

div.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

div.container > div.header {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  /* border : 3px solid blue; */
}

div.container > div.header > h2 {
  margin-top: -5px;
  margin-bottom: -2px;
  /* border : 1px solid orange; */
}

div.container > div.header > div {
  /*email*/
  /* border : 1px solid orange; */
}

div.container > div.nav {
  width: 15%;
  min-height: 85vh;
  /* border : 1px solid green; */
}

div.container > div.nav > ul {
  width: 100%;
  /* border : 1px solid yellow; */
}

div.container > div.nav > ul > li {
  width: 100%;
  padding-bottom: 10px;
}

div.container > div.main {
  width: 95%;
  min-height: 85vh;
}

div.container > div.main > h2 {
  margin: 10px;
}

@media (max-width: 700px) {
  div.container > div.nav {
    width: 100%;
    min-height: auto;
  }
  div.container > div.main {
    width: 100%;
    min-height: 100vh;
  }
  div.container > div.main > h2 {
    margin: 0;
  }
  div.container > div.nav > ul > li {
    width: auto;
  }
}

div.container > div.footer {
  width: 100%;
  text-align: center;
  /* border : 1px solid green; */
}

/*PORTFOLIO LIST*/
div.record {
  display: -ms-flexbox;
  display: flex;
  white-space: nowrap;
  -ms-flex-pack: spaced-between;
      justify-content: spaced-between;
  line-height: 24px;
}

div.head {
  font-weight: bold;
  margin-bottom: -10px;
  margin-top: -10px;
}

div.record > span.column {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  padding-left: 5px;
}

div.record > span.actionButton {
  margin: 2px;
}

span.pIdx {
  width: 3%;
}
span.pName {
  width: 30%;
}
span.pDesc {
  width: 80%;
}
span.pTech {
  width: 20%;
}
span.pGithub {
  width: 10%;
}
span.pUrl {
  width: 10%;
} /*not used*/

button.defButton {
  width: 70px;
  cursor: pointer;
  border: gray;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}

button.show {
  color: black;
}
button.hide {
  color: blue;
}

a {
  text-decoration: none;
  color: #1fbba6;
}
a:hover {
  color: orange;
}

/*PORTFOLIO FORM*/
form.portfolioForm > div {
  display: -ms-flexbox;
  display: flex;
  margin: 5px;
}

form.portfolioForm > div > span {
  width: 100px;
  line-height: 40px;
  vertical-align: middle;
}

form.portfolioForm > div > input,
select {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 5px;
  padding-top: 3px;
  border-radius: 5px;
  font-family: "Didact Gothic", sans-serif;
  font-size: 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 400px;
}

textarea,
div.marked {
  width: 400px;
  height: 300px;
  margin-right: 2px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 5px;
  padding-top: 3px;
  border-radius: 5px;
  font-family: "Didact Gothic", sans-serif;
  font-size: 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

/*LOGIN FORM*/

div.loginWrapper {
  margin: auto;
  width: 50%;
}

form.login {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 500px;
}
form.login > div {
  display: -ms-flexbox;
  display: flex;
  padding: 5px;
}

form.login > div > span {
  width: 100px;
  line-height: 40px;
  vertical-align: middle;
}

form.login > div > input {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 5px;
  padding-top: 3px;
  border-radius: 5px;
  font-family: "Didact Gothic", sans-serif;
  font-size: 16px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 300px;
}

@media (max-width: 700px) {
  form.login > div > input {
    width: 200px;
  }
}

@media (max-width: 470px) {
  div.loginWrapper {
    width: 100%;
  }
}

/*PAGE LIST*/

span.pTitle {
  width: 10%;
}
span.pContent {
  width: 40%;
}

/* GENERAL */

input.btn {
  cursor: pointer;
  width: 100px;
}

input.delete {
  background-color: rgb(255, 81, 81);
}

input.btn:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

input.delete:hover {
  background-color: rgb(255, 163, 163);
}



